<template><section><h3>基本用法(checkbox)</h3>
<hr>
<p><yj-checkbox v-model="checked">复选框1</yj-checkbox>
<yj-checkbox v-model="checked2">复选框2</yj-checkbox>
<yj-checkbox :checked="true">复选框2</yj-checkbox></p>

<p>在yj-checkbox元素中定义v-model绑定变量，单一的checkbox中，默认绑定变量的值会是Boolean，选中为true。</p>
<demo-block> <div slot="desc"><yj-checkbox v-model="checked"></yj-checkbox>
<yj-checkbox v-model="checked2"></yj-checkbox>
<yj-checkbox checked>复选框2</yj-checkbox>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">checked</span>&gt;</span>复选框2<span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">checked</span>:<span class="hljs-literal">true</span>,
      <span class="hljs-attr">checked2</span>:<span class="hljs-literal">false</span>
    }
  },
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>禁用状态(disabled)</h3>
<hr>
<p><yj-checkbox v-model="checked3" :disabled="true">复选框1</yj-checkbox>
<yj-checkbox v-model="checked4" :disabled="true">复选框2</yj-checkbox></p>
<demo-block> <div slot="desc"><yj-checkbox v-model="checked" disabled>复选框1</yj-checkbox>
<yj-checkbox v-model="checked2" disabled>复选框2</yj-checkbox>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span> <span class="hljs-attr">disabled</span>&gt;</span>复选框1<span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">disabled</span>&gt;</span>复选框2<span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">checked</span>:<span class="hljs-literal">true</span>,
      <span class="hljs-attr">checked2</span>:<span class="hljs-literal">false</span>
    }
  },
  
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Event(当绑定值变化时触发)</h3>
<hr>
<p><yj-checkbox v-model="checked5" @onChange="onChange">复选框1</yj-checkbox></p>
<demo-block> <div slot="desc"><yj-checkbox v-model="checked" @onChange="onChange">复选框1</yj-checkbox>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span> <span class="hljs-attr">@onChange</span>=<span class="hljs-string">"onChange"</span> &gt;</span>复选框1<span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">checked</span>:<span class="hljs-literal">false</span>
    }
  },
  <span class="hljs-attr">methods</span>:{
    onChange(e){
      alert(e)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>多选框组(yj-checkbox-group适用于多个勾选框绑定到同一个数组的情景)</h3>
<hr>
<yj-checkbox-group v-model="groupLists">
  <yj-checkbox label="小明"></yj-checkbox>
  <yj-checkbox label="小李"></yj-checkbox>
  <yj-checkbox label="小新"></yj-checkbox>
  <yj-checkbox label="隔壁老王" :disabled="true"></yj-checkbox>
</yj-checkbox-group>
<demo-block> <div slot="desc"><yj-checkbox-group v-model="groupLists">
  <yj-checkbox label="小明"></yj-checkbox>
  <yj-checkbox label="小李"></yj-checkbox>
  <yj-checkbox label="小新"></yj-checkbox>
  <yj-checkbox label="隔壁老王" disabled></yj-checkbox>
</yj-checkbox-group>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"groupLists"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"小明"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"小李"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"小新"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">yj-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"隔壁老王"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">yj-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">groupLists</span>:[<span class="hljs-string">'小明'</span>,<span class="hljs-string">'隔壁老王'</span>]
    }
  },
  <span class="hljs-attr">methods</span>:{
   
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>checkbox prop</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value / v-model</td>
<td>绑定值</td>
<td>boolean</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>label</td>
<td>只有在checkbox-group或者绑定对象类型为array时有效</td>
<td>String</td>
<td>—</td>
<td>''</td>
</tr>
<tr>
<td>checked</td>
<td>当前是否勾选</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>checkbox Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>onChange</td>
<td>当绑定值变化时触发</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
<h3>checkboxGroup prop</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value / v-model</td>
<td>绑定值</td>
<td>array</td>
<td>—</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>checkboxGroup Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>onChange</td>
<td>当绑定值变化时触发</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
   data(){
     return{
       checked:true,
       checked2:false,
       checked3:true,
       checked4:false,
       checked5:false,
       groupLists:['小明','隔壁老王']
     }
  },
  methods:{
    onChange(e){
      alert(e)
    }
  }
}
</script>


